import React from 'react';
import ReactDOM from 'react-dom';
//import Speech from 'speak-tts';
import './index.css';


class Square extends React.Component {
  
  originalWordList =  ["put", "will", "little", "out", "him", "could", "have", "with", "don't", "had", "what", "one", "there", "but", 
          "very", "just", "back", "about", "come", "went", "for", "some", "made", "the", "and", "a", "to", "said", "all",
        "you", "me", "she", "come", "go", "of", "where", "was", "far", "away", "am", "we", "her", "they", "as", "be",
      "at", "not", "then"];

  constructor(props) {
    super(props);
    
    this.state = 
      { 
        Words: this.originalWordList.slice(),
        CurrentWords: [],
        ButtonText : "Start",
        StopWatchRunning : false,
        StartTime : Date.now(),
        RunningTime : "0"
      };
    this.handleClick = this.handleClick.bind(this);
    //this.speech.init();
  }

  handleClick()
  {
    var remainingWords = this.state.Words;
    var stopWatchRunning = this.state.StopWatchRunning;
    var buttonText = "Next"

    if (remainingWords.length === 0)
    {
      if (stopWatchRunning)
      {
        stopWatchRunning = false;
        buttonText = "Well Done Kyan! Start Again?";
      }
      else
      {
        // we're starting again
        stopWatchRunning = true;
        remainingWords = this.originalWordList.slice();
      }
    }
    else
    {
      stopWatchRunning = true;
    }
    
    var newWords = []
    
    if (remainingWords.length > 0)
    {
      for (var i = 0; i < 5; ++i)
      {
        if (remainingWords.length === 0)
        {
          buttonText = "Done";
          newWords.push("-");
        }
        else
        {
          var index = Math.floor(Math.random() * remainingWords.length)
          newWords.push(remainingWords[index]);
          remainingWords.splice(index, 1);
        }
      }
  }

    this.setState(state => {
      
      if (stopWatchRunning && !this.state.StopWatchRunning)
      {
        this.setState({ StartTime: Date.now() });
        this.timer = setInterval(() => {
          var duration = Date.now() - this.state.StartTime
          var rt = Math.floor(duration/1000);
          this.setState({ RunningTime: rt });

          if (rt > 300)
            clearInterval(this.timer);
        });
      }
      else if (!stopWatchRunning)
        clearInterval(this.timer);
      
      return {
      Words : remainingWords,
      CurrentWords : newWords,
      ButtonText : buttonText,
      StopWatchRunning: stopWatchRunning
    }});

  }
  
  componentWillUnmount() {
    clearInterval(this.timer);
  }

  render() {


  const items = []

  for (const value of this.state.CurrentWords.entries()) {
    items.push(<div align="center">{value[1]}</div>)
  }
      return (
        <div>
          {items}
          <button className="square" onClick={this.handleClick}>
            {this.state.ButtonText}
          </button>
          <div align="center">{this.state.RunningTime} seconds</div>        
        </div>
      );
    }
  }
  
  // ========================================
  
  ReactDOM.render(
    <Square />,
    document.getElementById('root')
  );
  